import React from "react";
import {useDrop} from "react-dnd";
import {Card} from "./Card";
import {CardState, PlayingCard} from "interfaces/game";
import {useSelector} from "react-redux";
import {selectFoundations, selectTableuPiles} from "reducers/gameReducer";
import {RootState} from "store";
import {typeOfCard} from "dragTypes";

interface DraggedItem {
  type: string;
  card: PlayingCard;
}

interface FoundationProps {
  cards: PlayingCard[];
  index: number;
  foundationSuit: string;
  nextCard: number;
}

const getDroppable = (
  item: DraggedItem,
  nextCard: number,
  foundationSuit: string,
  tableuPile: PlayingCard[],
  foundations: PlayingCard[][],
  index: number
): boolean => {
  const id = item.card?.id;
  if (item?.card?.state === CardState.TableuPile) {
    if (item.card.id !== tableuPile[tableuPile.length - 1].id) {
      return false;
    }
  }
  if (item.card.rank === 1 && foundations?.[index].length === 0) {
    return true
  }

  return id === `${foundations?.[index]?.[foundations?.[index]?.length - 1]?.rank + 1}${foundations?.[index]?.[0]?.suit}`;

};

export const Foundation = ({cards, index, foundationSuit, nextCard}: FoundationProps): JSX.Element => {
  const foundations = useSelector<RootState, PlayingCard[][]>(selectFoundations);
  const nextState = CardState.Foundation;
  const tableuPiles = useSelector<RootState, PlayingCard[][]>(selectTableuPiles);
  // eslint-disable-next-line
  const [{canDrop, isOver}, drop] = useDrop({
    accept: typeOfCard,
    canDrop: (item: DraggedItem) => {
      return getDroppable(item, nextCard, foundationSuit, tableuPiles[item.card.index], foundations, index)
    },
    drop: () => ({
      index,
      nextState
    }),
    collect: monitor => ({
      isOver: monitor.isOver(),
      canDrop: monitor.canDrop()
    })
  });


  return (
    <div ref={drop} className="drop-target">
      <div className="card-border">
        {cards.map((card: PlayingCard, i: number) => (
          <Card offset={0} key={card.id} card={card} cards={cards} indexInWaste={i} isActive={isOver} gamePlace={'foundation'} isLastCard={i === cards.length - 1 ? true : false}/>
        ))}
      </div>
    </div>
  );
};
