import React from 'react'
import style from './style.module.scss'
import {Modal} from "reactstrap";
import logo from './assets/logo.svg'
import close from './assets/fi-rr-cross-small.svg'

export default function InstallApp({open, setOpen}) {

  let osCheck
  let mobile = false

  if (navigator.appVersion.indexOf("iPhone") !== -1) {
    mobile = true
    osCheck = "IOS"
  } else if (navigator.appVersion.indexOf("Android") !== -1) {
    mobile = true
    osCheck = "Android"
  } else {
    mobile = false
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <>
      {mobile && <Modal isOpen={open} className={style.install}>
        <div className={style.installBlock}>
          <div className={style.caption}>
            <div className={style.logo}>
              <img src={logo} alt="logo"/>
            </div>
            <div className={`${style.installTxt} mr-2 ml-2`}>
              <h3>Create this game</h3>
              <p>in the Gift Solitare App</p>
            </div>
          </div>
          <div className={`${style.icons}`}>
            <a className={style.installButtons} href="http://onelink.to/r8568s">
              Install
            </a>
          </div>
        </div>
        <div className={style.close} onClick={handleClose}>
          <img src={close} alt="close"/>
        </div>
      </Modal>}
    </>

  )
}
