import React from "react";
import {Modal, ModalBody, ModalFooter} from 'reactstrap';
import "./modal.scss"
import black_undo from "../../assets/black_undo.svg"
import play_icon from "../../assets/icon_play.svg"

export default function GameOverModal({modal, setModal, handleStartNewGame, handleUndoMove}) {

  return (
    <Modal className="game-over-modal" isOpen={modal}  toggle={() => setModal(s => !s)}>
      <ModalBody>
        <h5 className={'game-over-title'}>
          The Turns in the Game
          are Over</h5>
      </ModalBody>
      <ModalFooter>
        <div className={'game-over-options'}>
          <button onClick={handleUndoMove}>
            <img src={black_undo} alt=""/>
            Undo
          </button>
          <button onClick={handleStartNewGame}>
            <img src={play_icon} alt=""/>
            New Game
          </button>
        </div>
        <a className={`d-flex align-items-center justify-content-center`} href="https://giftsolitaire.com/">
          <button className={'exit'}>
            Exit Game
          </button>
        </a>
      </ModalFooter>
    </Modal>
  )
}
