import React, {useState} from "react";
import {Modal, ModalBody, ModalFooter} from 'reactstrap';
import "./modal.scss"

export default function StartNewGame({modal, setModal, onClick}) {
  const [block, setBlock] = useState(false)

  return (
    <Modal className="exit-modal" isOpen={modal}>
      <ModalBody>
        <h5 className={'exit-title'}>Start New Game</h5>
      </ModalBody>
      <ModalFooter>
        <button onClick={() => setModal(false)}>Back</button>
        <hr/>
        <button disabled={block} onClick={() => {
          setBlock(true);
          onClick();
          setTimeout(() => setBlock(false), 500)
        }}>Yes</button>
      </ModalFooter>
    </Modal>
  )
}
