import React from "react";
import {Talon} from "./Talon";
import {useDispatch, useSelector} from "react-redux";
import {drawCard, DrawCard} from "actions/gameActions";
import {selectDeck, selectDeckCount} from "reducers/gameReducer";
import {RootState} from "../store";
import {CardsState, getCards} from "../reducers/cardsReducer";

export const Stock = ({classes = "stocks"}): JSX.Element => {
  const deck = useSelector(selectDeck);
  const dispatch = useDispatch();

  const fetchedCards = useSelector<RootState, CardsState>(getCards);
  const deckCount = useSelector<RootState, number>(selectDeckCount);

  return (
    <div className={classes}>
      <div className="stock-opened-cards">
        <Talon/>
      </div>
      {/*<div/>*/}
      {Object.keys(fetchedCards.cards).length ? (deck.length > 0 && !(deckCount < 1)) ? (
        <picture>
          <div className={'drop-target card-backside playing-card'}>
            <img
              alt="deck"
              src={`data:image/png;base64,${fetchedCards.cards["1B"]}`}
              onClick={(): DrawCard => dispatch(drawCard())}
              onDragStart={(e): void => {
                e.preventDefault();
                e.stopPropagation();
              }}
            />
          </div>
        </picture>
      ) : (
        <div className="drop-target" onClick={(): DrawCard => dispatch(drawCard())}>
          <div className="card-border">
            <img
              alt="deck"
              src={require(`../assets/reload.svg`)}
              style={{alignSelf: "center"}}
              className="p-5"
              onDragStart={(e): void => {
                e.preventDefault();
                e.stopPropagation();
              }}
            />
          </div>
        </div>
      ) : <></>}
    </div>
  );
};
