import React from "react";
import {Card} from "./Card";
import {useSelector} from "react-redux";
import {selectDeckType, selectWaste} from "reducers/gameReducer";
import {RootState} from "store";
import {PlayingCard} from "interfaces/game";

export const Talon = (): JSX.Element => {
    const waste = useSelector<RootState, PlayingCard[]>(selectWaste);
    const deckType = useSelector<RootState, 1 | 3>(selectDeckType);
    return (
      <div className="drop-target" style={{alignSelf: "flex-end", display: "flex", flexDirection: "row-reverse"}}>
        {(deckType === 1) ?
          !!waste?.length && waste.map((card, i) => {
              return <Card key={card.id} card={card} isActive={false} cards={waste} gamePlace={'talon'}
                           isLastCard={i === waste.length - 1} indexInWaste={i + 1}/>
            }
          ) : (waste.length > 0 && deckType === 3) ?
            <>
              {
                !!waste?.length && waste.map((card, i) => {
                  return <Card key={card.id} card={card} isActive={false} cards={waste} gamePlace={'talon'}
                               indexInWaste={i}
                               isLastCard={i === waste.length - 1}/>
                })
              }
            </> : <></>
        }
      </div>
    );
  }
;
