import React from "react";
import {Modal, ModalBody, ModalFooter} from 'reactstrap';
import "./modal.scss"

export default function ExitGameModal({modal, setModal}) {

  return (
    <Modal className="exit-modal" isOpen={modal}>
      <ModalBody>
        <h5 className={'exit-title'}>The Game will be Over</h5>
      </ModalBody>
      <ModalFooter>
        <button onClick={() => setModal(false)}>Back</button>
        <hr/>
        <a className={`d-flex align-items-center justify-content-center`} href="https://giftsolitaire.com/">
          <button onClick={() => setModal(false)}>Yes</button>
        </a>
      </ModalFooter>
    </Modal>
  )
}
