import React from "react";

interface GameButtonProps {
  handleClick: Function;
  icon: string;
  text: string;
  styled: boolean;
}

export const GameButton = ({ icon, handleClick, text, styled }: GameButtonProps): JSX.Element => {
  return (
    <button className={`${styled && "game-styled-button"} game-button`} onClick={(): void => handleClick()}>
      <div className="button-wrapper">
        {icon && <img className="svg-icon" src={require(`../assets/${icon}.svg`)} alt={icon} />}
        <span className="button-text">{text}</span>
      </div>
    </button>
  );
};
