import React, {useEffect, useState} from "react";
import {Modal, ModalBody, ModalFooter} from 'reactstrap';
import "./modal.scss";
import ReactCodeInput from "react-code-input";
import {decks} from "../../utils";
import {setCards} from "../../actions/cardsActions";
import {useDispatch, useSelector} from "react-redux";
import deal_cards from "../../assets/sounds/deal_cards.wav";
import {getSound} from "../../reducers/gameReducer";

function playSound(dealCardsSound) {
  dealCardsSound.play()
}

export default function PINModal({setRequestState, requestState, setCong, modal, setModal, gameType}) {
  const {pathname} = window.location
  const [error, setError] = useState(null);
  const [pin, setPin] = useState("");
  const inputProps = {
    inputStyle: {
      height: 44,
      width: 40,
      border: !requestState.error ? "2px solid rgba(0,0,0,0.5)" : "2px solid #E73C3D",
      outlineColor: !requestState.error ? "rgba(0,0,0,0.5)" : "#E73C3D",
      // outlineColor: !error ? "#197532" : "#E73C3D",
      borderRadius: 5,
      fontSize: "25px",
      fontWeight: "700",
      textAlign: "center",
      boxShadow: "rgb(0 0 0 / 25%) 0px 0px 10px 0px",
    },
  };

  useEffect(() => {
    if (gameType.length) {
      if (gameType === "paid") {
        setModal(true)
      } else {
        onCLickPlay()
      }
    }
  }, [gameType])

  const dispatch = useDispatch();
  const sound = useSelector(getSound);

  const onCLickPlay = (e) => {
    e && e.preventDefault();
    setRequestState(requestState => ({...requestState, pending: true}))
    fetch(`https://giftsolitaire.com/api/v1/decks/web${pathname}${gameType === "paid" ? '/?pin=' + pin : ''}`)
      .then(res => {
        if (!res.ok) throw new Error(res)
        return res.json()
      })
      .then(cards => {
        const rewrittenFetchedCards = {};
        Object.keys(cards).forEach(card => {
          // @ts-ignore
          rewrittenFetchedCards[decks[card]] = cards[card]
        })
        setRequestState(requestState => ({...requestState, pending: false, fulfilled: true}))
        setModal(false);
        setCong(true);
        return rewrittenFetchedCards
      }).then(cards => {
      const dealCardsSound = new Audio(deal_cards);
      sound && playSound(dealCardsSound)
      dispatch(setCards(cards))
    }).catch(() => {
      setRequestState({pending: false, fulfilled: true, error: true})
    })
  }

  useEffect(() => {
    pin.length < 4 ?
      setError("Fields must be filled") :
      setError(null)
  }, [pin]);

  return (
    <Modal className="pin-modal" isOpen={modal}>
      <form onSubmit={onCLickPlay}>
        <ModalBody>
          <h5 className={'pin-title'}>Enter PIN-code</h5>
          <div className={"input-cont"}>
            <ReactCodeInput name="PIN-fields" type='number' fields={4} value={pin} onChange={e => {
              setPin(e)
            }}
                            inputMode={"numeric"}
                            {...inputProps}
            />
            <p className="description">If you do not know the PIN, then check
              with the person who sent you the link</p>
          </div>
        </ModalBody>
        <ModalFooter>
          <a className={`d-flex align-items-center justify-content-center`} href="https://giftsolitaire.com/">
            <button type="button">Cancel</button>
          </a>
          <hr/>
          <button disabled={error} type='submit'>Play</button>
        </ModalFooter>
      </form>
    </Modal>
  )
}
