import React from "react";
import {Modal, ModalBody, ModalFooter} from 'reactstrap';
import "./modal.scss"
import gift from "../../assets/icon_gift.svg"

export default function Congratulations({modal, setModal, congratulations, setInstall}) {

  return (
    <Modal className="gift-modal" isOpen={modal} toggle={() => {
      setModal(s => !s)
      setInstall(true)
    }}>
      <ModalBody style={{minHeight: 250}} className={`d-flex align-items-center flex-column`}>
        <img src={gift} className={`mb-3`} alt=""/>
        <h5 className={'gift-title text-center'}>
          {congratulations}
        </h5>
      </ModalBody>
      <ModalFooter className={`d-flex justify-content-center align-items-start`}
                   style={{borderTop: "none", height: 80}}>
        <button className={`gift-close-btn`} onClick={() => {
          setModal(s => !s)
          setInstall(true)
        }}>Ok. Thanks!
        </button>
      </ModalFooter>
    </Modal>
  )
}
