import produce, {Draft} from "immer";
import {CardsActions, CardsActionsTypes, Cards} from "actions/cardsActions";
import {RootState} from "store";
// import {PlayingCard} from "interfaces/game";

export interface CardsState {
  cards: Cards;
}

export const initialState: CardsState = {
  cards: {
  },
};

export const cardsReducer = (state = initialState, action: CardsActions): CardsState => {
  switch (action.type) {
    case CardsActionsTypes.GetCards:
      return produce(state, (draft: Draft<CardsState>) => {
        draft.cards = action.payload;
      });
    default:
      return state;
  }
};

export const getCards = (state: RootState):CardsState => state.cards;
