export enum CardsActionsTypes {
  GetCards = "cards/GetCards"
}

export type Cards = {
  [key: string]: string
}

export type GetCards = {
  readonly type: CardsActionsTypes.GetCards;
  readonly payload: Cards
}

export const setCards = (payload: Cards): CardsActions => ({
  type: CardsActionsTypes.GetCards,
  payload
})

export type CardsActions =
  | GetCards
