import React, {useEffect, useState} from "react";
import {TableuPile} from "components/TableuPile";
import {Stock} from "components/Stock";
import {Foundation} from "components/Foundation";
import {
  getSound, getUndoStepsCount,
  selectDrawCounts,
  selectFoundations,
  selectScore,
  selectTableuPiles,
  selectWaste
} from "reducers/gameReducer";
import {useDispatch, useSelector} from "react-redux";
import {GameActions, startNewGame, undoMove} from "actions/gameActions";
import {PlayingCard} from "interfaces/game";
import {createNewGame, suits} from "utils";
import {RootState} from "store";
import {CardDragPreview} from "components/CardDragPreview";
import {Dispatch} from "redux";
import {Timer} from "components/Timer";
import {GameButton} from "components/GameButton";
import {getScores, ScoreActions} from "actions/scoreActions";
import {selectGameWonTime, selectIsGameOver} from "reducers/scoreReducer";
import {clearHistory, HistoryActions} from "actions/historyActions";
import setting_icon from "../assets/setting.svg";
import Loader from "../components/Modals/Loader";
import PINModal from "../components/Modals/PIN-modal";
import GameOverModal from "../components/Modals/GameOverModal";
import ExitGameModal from "../components/Modals/ExitGameModal";
import SettingsModal from "../components/Modals/SettingsModal";
import StartNewGame from "../components/Modals/StartNewGame";
import InstallApp from "../components/Modals/InstallApp";
import Win from "../components/Modals/Win/index";
import card_return from "../assets/sounds/card_return.wav";
import deal_cards from "../assets/sounds/deal_cards.wav";
import Congratulations from "../components/Modals/Congratulations";
import {ActionCreators} from "redux-undo";

const dealCardsSound = new Audio(deal_cards);
const cardReturnSound = new Audio(card_return);
const Game = (): JSX.Element => {
  const [requestState, setRequestState] = useState({
    pending: true,
    fulfilled: false,
    error: null
  });
  const [gameOverModal, setGameOverModal] = useState(false);
  const [openInstall, setOpenInstall] = useState(false)
  const [congratulationsM, setCongratulationsM] = useState(false);
  const [gameType, setGameType] = useState('');
  const [exitModalOpen, setExitModalOpen] = useState(false);
  const [startNewGameModal, setStartNewGameModal] = useState(false);
  const [settingsModalOpen, setSettingsModalOpen] = useState(false)
  const dispatch = useDispatch<Dispatch<GameActions | ScoreActions | HistoryActions>>();
  const tableuPiles = useSelector<RootState, PlayingCard[][]>(selectTableuPiles);
  const foundations = useSelector<RootState, PlayingCard[][]>(selectFoundations);
  const gameWonTime = useSelector<RootState, string | undefined>(selectGameWonTime);
  const isGameOver = useSelector<RootState, boolean>(selectIsGameOver);
  const drawCount = useSelector<RootState, number>(selectDrawCounts);
  const sound = useSelector<RootState, boolean>(getSound);
  const [pinCodeModal, setPinCodeModal] = useState(false);
  const [congratulations, setCongratulations] = useState('')

  useEffect(() => {
    const {tableuPiles, deck} = createNewGame();
    dispatch(startNewGame({tableuPiles, deck}));
  }, [dispatch]);
  useEffect(() => {
    const scores = JSON.parse(localStorage.getItem("scores") || "{}") as number[];
    if (scores && scores.length > 0) {
      dispatch(getScores(scores));
    }
  }, [dispatch]);

  useEffect(() => {
    (drawCount !== 0 && drawCount % 5 === 0) && setGameOverModal(true);
  }, [drawCount])

  const undoStepsCount = useSelector<RootState, number>(getUndoStepsCount)

  const handleUndoMove = (): void => {
    sound && cardReturnSound.play()
    // dispatch(undoMove());
    dispatch(ActionCreators.jump(undoStepsCount))
    setGameOverModal(false);
  };

  const handleStartNewGame = (): void => {
    setStartNewGameModal(false);
    setGameOverModal(false);
    const {tableuPiles, deck} = createNewGame();
    dispatch(clearHistory());
    dispatch(startNewGame({tableuPiles, deck}));
    sound && dealCardsSound.play();
  };

  const {pathname} = window.location;

  useEffect(() => {
    fetch(`https://giftsolitaire.com/api/v1/decks/web/check${pathname}`)
      .then(res => res.json())
      .then(({congratulations, is_demo}) => {
        setCongratulations(congratulations)
        setGameType(!is_demo ? "paid" : "demo")
        // @ts-ignore
        setRequestState({pending: false, fulfilled: false, error: false})
      }).catch((error) => {
      // @ts-ignore
      setRequestState({pending: false, fulfilled: false, error: false})
    })
  }, [])

  const gameScores = useSelector<RootState, number[]>(selectScore)
  const waste = useSelector<RootState, PlayingCard[]>(selectWaste)

  return (
    <>
      <Loader requestState={requestState}/>
      <GameOverModal handleStartNewGame={handleStartNewGame} handleUndoMove={handleUndoMove} modal={gameOverModal}
                     setModal={setGameOverModal}/>
      <ExitGameModal modal={exitModalOpen} setModal={setExitModalOpen}/>
      <StartNewGame modal={startNewGameModal} setModal={setStartNewGameModal} onClick={handleStartNewGame}/>
      <SettingsModal setModal={setSettingsModalOpen} modal={settingsModalOpen}/>
      <PINModal setRequestState={setRequestState} gameType={gameType} setCong={setCongratulationsM}
                requestState={requestState} modal={pinCodeModal}
                setModal={setPinCodeModal}/>
      <Congratulations setModal={setCongratulationsM} setInstall={setOpenInstall} modal={congratulationsM}
                       congratulations={congratulations}/>
      <InstallApp open={openInstall} setOpen={setOpenInstall}/>
      {isGameOver && <Win scores={gameScores} gameWonTime={gameWonTime}/>}
      <CardDragPreview tableuPiles={tableuPiles}/>
      <div className="mainCont">
        <div className="head">
          <GameButton handleClick={() => {
            setExitModalOpen(true)
          }} icon="right-arrow" text={""} styled={false}/>
          <div className="game-widgets">
            {/*<GameButton icon="trophy" handleClick={handleScores} text="SCORES"/>*/}
            <p
              className={`m-0 d-flex align-items-center score`}>Score: {gameScores.length ? gameScores.reduce((a, b) => a + b) : 0}</p>
            <Timer/>
            <button onClick={() => setSettingsModalOpen(true)} className='settings-widget'>
              <img src={setting_icon} alt=""/>
            </button>
          </div>
        </div>
        <div className="desc">
          <div className="desc-head">
            <div className="foundations">
              {foundations.length &&
              foundations.map((foundation, i) => {
                return (
                  <Foundation
                    key={i.toString()}
                    cards={foundation}
                    index={i}
                    foundationSuit={suits[i]}
                    nextCard={foundation.length + 1}
                  />
                )
              })}
            </div>
            <Stock classes={"stocks top-stock"}/>
          </div>
          <div className="desc-right">
            <div className="tableu-piles">
              {tableuPiles.map((pile, i) => (
                <TableuPile cards={pile} key={i.toString()} index={i}/>
              ))}
            </div>
            <Stock classes={"stocks bottom-stock"}/>
          </div>
        </div>
      </div>
      <div className="game-action-buttons">
        {!isGameOver && <GameButton icon="" handleClick={() => setStartNewGameModal(true)} text="New game" styled={true}/>}
        {!!(gameScores?.length || waste.length) && !isGameOver &&
        <GameButton icon="undo" handleClick={handleUndoMove} text="Undo" styled={true}/>}
      </div>
    </>
  );
}

export default Game;
