import React, {memo, useEffect} from "react";
import {DragSourceMonitor, useDrag} from "react-dnd";
import {useDispatch, useSelector} from "react-redux";
import {
  addCardsToFoundation,
  addCardsToTableuPile,
  cardIsDragged,
  handleDoubleClick,
  turnCard,
  TurnCard
} from "actions/gameActions";
import {CardProps, CardState, PlayingCard} from "interfaces/game";
import {selectDeckType, selectNextFoundationCards} from "reducers/gameReducer";
import {getEmptyImage} from "react-dnd-html5-backend";
import {Dispatch} from "redux";
import {RootState} from "../store";
import {CardsState, getCards} from "../reducers/cardsReducer";
import {selectIsGameOver} from "../reducers/scoreReducer";

export const Card = memo(
  ({card, offset, cards, isLastCard, isActive, gamePlace, indexInWaste}: CardProps): JSX.Element => {
    const {hidden, id, index, type} = card;
    const dispatch = useDispatch();
    const nextFoundationCards = useSelector(selectNextFoundationCards);
    const [{isDragging, item}, drag, preview] = useDrag({
      item: {type, card},
      end: (item: { type: string; card: PlayingCard } | undefined, monitor: DragSourceMonitor) => {
        const dropResult = monitor.getDropResult();
        if (item && dropResult) {
          const nextState = dropResult.nextState;
          if (nextState === CardState.TableuPile) {
            dispatch(addCardsToTableuPile({movedCard: item.card, index: dropResult.index, nextState}));
          } else if (nextState === CardState.Foundation) {
            dispatch(addCardsToFoundation({movedCard: item.card, index: dropResult.index, nextState}));
          }
        }
        if (item && !dropResult) {
          dispatch(cardIsDragged({movedCard: item.card, isDragging: false, preview}));
        }
        if (cards.length >= 2 && gamePlace === "pile") {
          const openedCardsAmount = cards.filter(card => !card.hidden)
          const lastAfterDragCard = cards[cards.length -
          ((
              openedCardsAmount.length > 1 && openedCardsAmount.length !== cards.length) ?
              (openedCardsAmount.length + 1) :
              2
          )]
          dispatch(turnCard({index: lastAfterDragCard.index, id: lastAfterDragCard.id}))
        }
      },
      collect: monitor => ({
        isDragging: monitor.isDragging(),
        item: monitor.getItem()
      }),
      canDrag: () => (card.hidden ? false : true)
    });

    const checkDoubleClick = (card: PlayingCard, nextFoundationCards: string[], dispatch: Dispatch): void => {
      if (nextFoundationCards.find(nextCard => nextCard === card.id)) {
        dispatch(handleDoubleClick({doubleClickedCard: card}));
      }
      if (gamePlace === "pile") {
        if (cards.length >= 2) {
          const lastAfterDragCard = cards[cards.length - 2]
          dispatch(turnCard({index: lastAfterDragCard.index, id: lastAfterDragCard.id}))
        }
      }
    };
    useEffect(() => {
      if (item && item.card && isDragging) {
        dispatch(cardIsDragged({movedCard: item.card, isDragging, preview}));
      }
    }, [isDragging, item, dispatch]);

    useEffect(() => {
      preview(getEmptyImage(), {captureDraggingState: false});
    }, [preview]);

    function isBackside() {
      if (gamePlace !== "talon") {
        return (hidden)
      }
    }

    function isDraggable() {
      if (gamePlace !== "talon") {
        return (hidden && !isLastCard)
      } else {
        return isLastCard
      }
    }

    const isGameOver = useSelector<RootState, boolean>(selectIsGameOver);
    const fetchedCards = useSelector<RootState, CardsState>(getCards);
    const deckType = useSelector<RootState, 1 | 3>(selectDeckType);

    return Object.keys(fetchedCards.cards).length ? (
      <>{isBackside() ?
        <div className={'card-backside playing-card'}
             style={{top: offset, opacity: isActive ? .7 : 1}}
        >
          <img
            alt="deck"
            src={`data:image/png;base64,${fetchedCards.cards["1B"]}`}
            onDragStart={(e): void => {
              e.preventDefault();
              e.stopPropagation();
            }}
          />
        </div> :
        <img
          alt={card.id}
          ref={(!isGameOver && isDraggable) ? drag : null}
          className={`playing-card`}
          src={`data:image/png;base64,${fetchedCards.cards[card.id]}`}
          style={{
            top: offset,
            right: (gamePlace === "talon" && deckType === 3) ? `${indexInWaste === 2 ? 'unset' : indexInWaste === 1 ? 20 : 40}px` : "unset",
            display: card.isDragging ? "none" : undefined,
            opacity: isActive ? .7 : 1
          }}
          onClick={(): TurnCard => dispatch(turnCard({index, id}))}
          onDoubleClick={isLastCard ? (): void => checkDoubleClick(card, nextFoundationCards, dispatch) : undefined}
          onDragStart={(e): void => {
            if (card.hidden) {
              e.preventDefault();
              e.stopPropagation();
            }
          }}
        />}
      </>
    ) : <></>
  }
);

export default Card;
