import React, {useEffect, useState} from "react";
import {Modal, ModalBody, ModalFooter} from 'reactstrap';
import "./modal.scss"
import {useDispatch, useSelector} from "react-redux";
import {changeDeckDrawType, startNewGame, switchSound} from "../../actions/gameActions";
import {createNewGame} from "../../utils";
import {selectDeckType} from "../../reducers/gameReducer";

const CreateRadio = ({label, setState, state}) => (
  <div onClick={() => setState(s => !s)} className={`setting-radio`}>
    <p className={`mr-2`}>{label}</p>
    <div className={`field ${state && "active"}`}>
      <div className={`radio-circle`}/>
    </div>
  </div>
)

export default function SettingsModal({modal, setModal}) {
  const [animation, setAnimation] = useState(false);
  const [agreement, setAgreement] = useState(false);
  const [sound, setSound] = useState(true);
  const [stockStatus, setStockStatus] = useState(1);
  const dispatch = useDispatch();
  const {tableuPiles, deck} = createNewGame();

  useEffect(() => {
    dispatch(switchSound(sound))
  }, [sound])

  const deckType = useSelector(selectDeckType)

  function onAgreeToStartNew() {
    setAgreement(false)
    dispatch(changeDeckDrawType(stockStatus));
    dispatch(startNewGame({tableuPiles, deck}));
  }

  function onClickRadio(type) {
    setStockStatus(type);
  }

  return (
    <>
      {
        !agreement ?
          <Modal className="setting-modal" isOpen={modal}>
            <ModalBody>
              <h5 className={'setting-title'}>Game Settings</h5>
              <p>Dealing from the Stock</p>
              <div className='stock-switch'>
                <button className={stockStatus === 1 && "active"} onClick={() => onClickRadio(1)}>Turning 1 card
                </button>
                <button className={stockStatus === 3 && "active"} onClick={() => onClickRadio(3)}>Turning 3 cards
                </button>
              </div>
              <div className={'setting-radios mt-3'}>
                <CreateRadio label={'Animation'} state={animation} setState={setAnimation}/>
                <CreateRadio label={'Sound'} state={sound} setState={setSound}/>
              </div>
            </ModalBody>
            <ModalFooter>
              <button onClick={() => {
                (stockStatus !== deckType) && setAgreement(true)
                setModal(false)
              }}>Close
              </button>
            </ModalFooter>
          </Modal> :
          <Modal className="setting-modal-agreement" isOpen={agreement}>
            <ModalBody>
              <h5 className={'setting-title'}>After changing the settings, <br/> the game will start over!</h5>
            </ModalBody>
            <ModalFooter>
              <button onClick={() => {
                setStockStatus(deckType);
                setModal(true)
                setAgreement(false);
              }}>Close
              </button>
              <hr/>
              <button onClick={onAgreeToStartNew}>Start new game</button>
            </ModalFooter>
          </Modal>
      }
    </>
  )
}
