import React from 'react'
import style from './style.module.scss'
import img from './assets/icon_cup.svg'
import win from "../../../assets/sounds/win.wav";
import {useSelector} from "react-redux";
import {getSound} from "../../../reducers/gameReducer";

const winSound = new Audio(win);
export default function Win({scores, gameWonTime}) {
  const sound = useSelector(getSound);
  sound && winSound.play();
  return (
    <div className={style.win}>
      <div className={style.img}>
        <img src={img} alt="img"/>
      </div>

      <div className={style.contentText}>
        <h1>You Win!</h1>
        <div className={`d-flex align-items-center`}>
          {scores && <p className="score">
            Score: {scores.length ? scores.reduce((a, b) => a + b) : 0}
          </p>}
          <p>Time: {gameWonTime}
          </p>
        </div>
      </div>
    </div>
  )
}
